export default {
	props:['item'],
	data() {
		return {
			videoContext:''
		};
	},
	created() {
		
	},
	methods:{
		play(){
			this.videoContext = uni.createVideoContext('myFollowPlay',this)
			// console.log(this.videoContext.pageVm);
			this.videoContext.play()
		},
		pause(){
			this.videoContext = uni.createVideoContext('myFollowPlay',this)
			this.videoContext.pause()
		}
	}
}
